.user_menu_container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background: linear-gradient(to bottom,rgb(3, 124, 253),rgb(102, 168, 255));
  border-radius: 10px;
  height: 750px;
  width: 250px;
  transition: 500ms;
}
.user_menu_inactive {
  width: 0px;
  transition: 500ms;
}

.li {
  color: white;
  font-size: 18px;
  font-weight: 500;
}

@media screen and (max-width: 768px) {
  .user_menu_container {
    position: fixed;
    margin-right: 5px;
    height:600px;
    width: 200px;
  }
}
